.confirm-transfer-header {
  position: relative;
}
.confirm-transfer-header::before {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 14px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 93px;
  height: 93px;
  border-radius: 50%;
  background: linear-gradient(90deg, #f9575e 0%, #5d6ccf 100%);
  z-index: 9;
}
.confirm-transfer-header .sentre-col {
  padding: 7px 16px;
}
.confirm-transfer-header .sentre-col:first-child {
  border-radius: 8px 0 0 8px;
  background: #f9575e;
}
.confirm-transfer-header .sentre-col:nth-of-type(2) {
  border-radius: 0 8px 8px 0;
  text-align: right;
  background: #5d6ccf;
}

